
$Maincolor : black;

.red {
    color: $Maincolor;
}


.wow-enter {
    opacity: 0;
}
.wow-enter-active {
    opacity: 1;
    transition: all 500ms;
}


@mixin function() {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert {
    @include function();
}

.my-alert2 {
    @extend .my-alert;
    background: #ffe591;
}

.hidden {
    display: none;
}

.my-alert p {
    margin-bottom: 0;
}

